	},
	methods: {
		handleLogin(){
			// console.log('handleLogin')
			uni.getUserProfile({
				desc:"用于测试登录功能",
				success:(res)=>{
					// console.log('res',res)
					const {rawData} =res;
					uni.setStorage({
						key:"userInfoStr",
						data:rawData
					})
					uni.reLaunch({
						url:"/pages/personal/personal"
					})
				}
			})
		}
	}
}
